legend {
  margin-bottom: 1.5rem;
}

fieldset {
  border: none;
  padding: 0;
  margin: 0;
}

label {
  display: block;
  margin-bottom: 10px;

  &.hidden {
    height: 0 !important;
    width: 0 !important;
    font-size: 0 !important; 
    margin: 0 !important; // override custom styles
  }
}

button {
  border: none;
}

input {
  display: block;
  width: 100%;
  padding: 8px;

  @include placeholder-styles() {
    color: $text-placeholder;
  }

  &:focus {
    outline: 0;
  }

  &[type=radio],
  &[type=checkbox] {
    height: auto;
  }

  &[type=submit] {
    width: auto;
    border: none;
  }
}

input[type=text],
input[type=number],
textarea {
  border: 1px solid $text;
  border-radius: 8px;
  background-color: $white;
  font-size: 16px;
  line-height: 22px;
  padding: 12px 50px 12px 17px;
  transition: border-color 0.25s ease, border-width 0.25s ease background-color 0.25s ease;
  color: $text-dark;

  &:hover:not(:focus):not(.input-validation-error):not(:disabled):not([disabled="true"]) {
    border-color: $text;
  }

  &:hover:not(:disabled):not([disabled="true"]) {
    border-color: $blue;
  }

  &:focus-within {
    border-color: $dark-blue;
    border-width: 2px;
    padding: 11px 49px 11px 16px;
  }

  &:disabled,
  &[disabled="true"] {
    border-color: $grey-8;
    cursor: not-allowed;
  }

  &.input-validation-error,
  &.valid {
    background-repeat: no-repeat;
    background-position: right 23px top 17px;
  }

  &.input-validation-error {
    border-width: 2px;
    border-color: $red;
    background-image: url(/assets/dist/images/icons/sprite-icon-cross-red.svg);
    background-size: 12px;
    padding: 11px 49px 11px 16px;
  }

  &.valid {
    background-image: url(/assets/dist/images/icons/sprite-icon-checkbox-tick-blue.svg);
    background-size: 15px 11px;
  }
}

textarea {
  width: 100%;
  min-height: 168px;
  resize: none;
}

.input-wrap {
  position: relative;
}

select {
  width: 100%;
}

select {
  appearance: none;
  margin: 0;
  padding: 12px 50px 12px 17px;
  background-color: $white;
  border: 1px solid $text;
  border-radius: 8px;
  outline: none;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: $text-dark;

  &::-ms-expand {
    display: none;
  }

  &:hover {
    border-color: $blue;
  }

  &:focus-within {
    border-color: $dark-blue;
    border-width: 2px;
    padding: 11px 49px 11px 16px;
  }

  &:disabled {
    border-color: $grey-8;
    cursor: not-allowed;
  }
  
  &:not([multiple]) {
    background-image: linear-gradient(
      45deg,
      transparent 27%,
      $blue 27%,
      $blue 34%,
      transparent 34%
    ),
    linear-gradient(
      -45deg,
      transparent 27%,
      $blue 27%,
      $blue 34%,
      transparent 34%
    );
    background-size: 18px 12px;
    background-position: right 22px center, right 22px center;
    background-repeat: no-repeat;

    &:focus-within {
      background-image: linear-gradient(
        45deg,
        transparent 66%,
        $blue 66%,
        $blue 73%,
        transparent 73%
      ),
      linear-gradient(
        -45deg,
        transparent 66%,
        $blue 66%,
        $blue 73%,
        transparent 73%
      );          
    }

    &:disabled {
      background-image: linear-gradient(
        45deg,
        transparent 66%,
        $grey-8 66%,
        $grey-8 73%,
        transparent 73%
      ),
      linear-gradient(
        -45deg,
        transparent 66%,
        $grey-8 66%,
        $grey-8 73%,
        transparent 73%
      );
    }
  }

  select[multiple] {
    padding-right: 17px;

    option {
      white-space: normal;
    }
  }

  option {
    font-size: 16px;
    line-height: 20px;
    background-color: $light-grey;
    color: $black;

    &:hover {
      background-color: $grey;
      box-shadow: 0 0 10px 100px $black inset;
    }

    &:checked {
      background-color: $grey-2;
    }
  }
}

input[type=radio],
input[type=checkbox] {
  position: absolute;
  left: -9999px;
}

.radio-wrapper,
.checkbox-wrapper {
  label {
    position: relative;
    padding-left: 32px;
    margin-bottom: 15px;
    cursor: pointer;
    user-select: none; 
  }

  // Inline styles
  &.inline {
    label {
      display: inline-block;
      margin-right: 16px;
    }
  } 
}

.radio-wrapper {
  label {
    &:before {
      content: '';
      position: absolute;
      width: 24px;  
      height: 24px; 
      top: -2px;
      left: 0;
      background-color: $white;
      border-radius: 100%;
      border: 4px solid $white;
      box-shadow: 0 0 0 1px $grey-12;
      box-sizing: border-box;
      transition: background-color 0.2s ease;
    }
  }

  input[type=radio]:focus-visible + label,
  input[type=radio]:focus-visible + input + label {
      outline: 2px solid $black;
      outline-offset: 2px;
  }

  input[type=radio]:checked + label,
  input[type=radio]:checked + input + label {
    &:before {
      background-color: $blue;
    }
  }

  &.inline {
    display: flex;
    flex-wrap: wrap;
    gap: 4px;

    label {
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 8px;
      padding-left: 0;
      margin-right: 0;

      &::before {
        position: relative;
        display: block;
        top: unset;
        left: unset;
      }
    }
  }

  &:not(.inline) {
    label {
      display: inline-block;
    }
  }
}

.checkbox-wrapper {
    label {
      color: $text-dark;

      &:before {
        content: '';
        position: absolute;
        width: 24px;
        height: 24px;
        top: 0;
        left: 0;
        background-color: $white;
        border: 1px solid $grey-12;
        border-radius: 4px;
        box-sizing: border-box;
        background-size: 16px auto;
        background-position: center;
        background-repeat: no-repeat;
      }
    }

    input[type=checkbox]:focus-visible + label,
    input[type=checkbox]:focus-visible + input + label {
        outline: 2px solid $black;
        outline-offset: 2px;
    }

    input[type=checkbox]:checked + label,
    input[type=checkbox]:checked + input + label {
        &:before {
          background-color: $blue;
          border-color: $blue;
          background-image: url(/assets/dist/images/icons/sprite-icon-checkbox-tick-white.svg);
        }
    }
}

.field-validation-error {
  @include font-size(13);
  display: block;
  margin-top: 10px;
  padding-left: 18px;
  color: $red;
  background-image: url(/assets/dist/images/icons/sprite-icon-alert-xdark-red.svg);
  background-repeat: no-repeat;
  background-position: left top 4px;
  background-size: 12px;
}

@media(min-width:$min-desktop) {
  .umbraco-forms-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    .umbraco-forms-field {
      width: 100%;
    }

    .dropdown{
        width:48%;
    }
/*
    .multiplechoice, .radiobuttons, .singlechoice, .dataconsent {
      width: 100%;
    }*/
  }
}

/* Calculators */
.calculator-form {
  display: grid;
  grid-template-columns: auto;
  gap: 24px;

  @include media-min($small) {
    grid-template-columns: repeat(2, minmax(0, 438px));
    justify-content: space-between;
    gap: 32px;
  }

  fieldset {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
  }

  .field {
    &:not(:last-child) {
      margin-bottom: 24px;
    }

    + h4 {
      margin-top: 24px;

      @include media-min($small) {
        margin-top: 56px;
      }
    }

    &.button-wrapper {
      margin-top: auto;
    }
    
    &.error {
      input {
        border: 2px solid $xdark-red;
      }

      select {
        border: 2px solid $xdark-red;
    
        &:not([multiple]) {
          background-image: linear-gradient(
            45deg,
            transparent 27%,
            $xdark-red 27%,
            $xdark-red 34%,
            transparent 34%
          ),
          linear-gradient(
            -45deg,
            transparent 27%,
            $xdark-red 27%,
            $xdark-red 34%,
            transparent 34%
          );
        }
      }
    }
  }

  .calculation,
  .calculation .text-right {
    display: grid;
    grid-template-columns: auto;
    row-gap: 24px;

    @include media-min($small) {
      grid-template-columns: repeat(2, minmax(0, 438px));
    }
  }

  .calculation {
    padding: 24px;
    border-radius: 8px;
    background-color: $white; 
    grid-template-columns: auto;

    @include media-min($large) {
      grid-template-columns: 1fr minmax(0, 414px);
      column-gap: 32px;
      justify-content: space-between;
    }

    .text-right {
      column-gap: 8px;
      row-gap: 12px;

      @include media($small) {
        justify-content: unset;

        .button {
          width: 100%;
        }
      }
    }

    h4 {
      margin-bottom: 1.5rem;
      font-size: 32px;
      line-height: 1;
    }

    #final-amount {
      font-size: 24px;
      line-height: 30px;
      font-weight: 600;
      color: $text-dark;
    }

    .fee-info {
      display: flex;
      align-items: flex-start;
      gap: 8px;
      padding-top: 12px;

      &__icon {
        flex-shrink: 0;
        color: $text-dark;

        svg {
          width: 24px;
          height: 24px;
        }
      }

      &__content {
        @include last-child-no-bottom();

        > span {
          display: block;
          font-size: 18px;
          font-weight: 700;
          color: $text-dark;
        }
      }
    }
  }

  .calculation-wrapper {
    @include media-min($small) {
      grid-column: span 2;
    }

    &.error {
      .calculation {
        h4 {
          font-size: 16px;     
        }
      }
    }
  }

  #min-loan {
    font-size: 40px;
    line-height: 1;
    font-weight: 600;
    color: $text-dark;
  }
}